@import 'colors';

nav{
	width: 100%;
	padding: 1rem;
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: sticky;
	background-color: $first;
	top: 0%;
	z-index: 10;
/* 	opacity: 0.4; */

	>h1{
		text-transform: uppercase;
		
	}
	>main{
		width: 70%;
		display: flex;
		justify-content: flex-end;
		font-weight: bolder;
		>a{
			margin: 0.5rem;
		}
	}

		> Link{
			color: $dark;
			margin: 1rem;

		    &:hover{
				color: $third;
			}
		}

}
